}

.Navbar_link {
    border: none;
    background: none;
    color: #FFF9C7;
    text-decoration: none;
    position: relative;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.15em;
    
    display: inline-block;
    padding: 15px 20px;
    float: left;
}

.Navbar_link:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}

.Navbar_link:hover:after { 
    width: 100%; 
    left: 0; 
}

